.header-example {
  background-color: #ffffff;
  padding: 20px 0;
  transition: transform 0.3s ease, background-color 0.3s ease;
  z-index: 30;
}
.header-example--sticky {
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
}
.header-example--base {
  background-color: #ffffff;
}
.header-example--blue {
  background-color: #3c69ff;
}
.header-example--red {
  background-color: #f03838;
}
.header-example--green {
  background-color: #17ff45;
}
.header-example--yellow {
  background-color: #f4ff25;
}
.header-example.is-hidden {
  transform: translateY(-100%);
}
.header-example .container {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.header-example__logo {
  display: flex;
  margin: -8px 40px 0 0;
}
@media (max-width: 1279px) {
  .header-example__logo {
    flex-direction: column;
    transform: scale(0.85);
  }
}
@media (max-width: 1023px) {
  .header-example__logo {
    flex-direction: row;
    transform: scale(1);
  }
}
.is-open .header-example__logo {
  position: relative;
  z-index: 25;
}
@media (max-width: 1279px) {
  .header-example__logo .logo__second-part {
    margin: 0;
  }
}
@media (max-width: 1023px) {
  .header-example__logo .logo__second-part {
    margin: 0 0 0 9px;
  }
}
@media (max-width: 767px) {
  .header-example__logo .logo__second-part {
    margin: 0;
  }
}
.header-example__git-link {
  display: flex;
  margin: 0 0 0 auto;
}
@media (max-width: 1023px) {
  .header-example__git-link {
    margin: 0 24px 0 auto;
  }
}
@media (max-width: 1023px) {
  .header-example__nav {
    background-color: #2c39f2;
    bottom: 0;
    display: flex;
    flex-direction: column;
    left: 0;
    margin: 0;
    opacity: 0;
    padding: 123px 20px 20px 20px;
    pointer-events: none;
    position: fixed;
    right: 0;
    top: 0;
    transition: all 0.3s ease;
    visibility: hidden;
    z-index: 20;
  }
}
.header-example--blue .header-example__nav {
  background-color: #3c69ff;
}
.header-example--red .header-example__nav {
  background-color: #f03838;
}
.header-example--green .header-example__nav {
  background-color: #17ff45;
}
.header-example--yellow .header-example__nav {
  background-color: #f4ff25;
}
.is-open .header-example__nav {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}
.is-open .header-example__nav .header-example__item {
  opacity: 1;
  transform: translateX(0);
}
.header-example__list {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  max-width: 450px;
  padding: 0;
}
@media (max-width: 1023px) {
  .header-example__list {
    flex-direction: column;
    margin: 0 0 70px 0;
    max-width: unset;
  }
}
@media (max-width: 767px) {
  .header-example__list {
    align-items: flex-start;
    margin: 0 0 62px 0;
  }
}
@media (max-width: 1023px) {
  .header-example__item {
    opacity: 0;
    transform: translateX(-15px);
    transition: all ease-out 0.15s;
  }
}
.header-example__item:not(:last-child) {
  margin: 0 55px 0 0;
}
@media (max-width: 1279px) {
  .header-example__item:not(:last-child) {
    margin: 0 35px 0 0;
  }
}
@media (max-width: 1023px) {
  .header-example__item:not(:last-child) {
    margin: 0 0 40px 0;
  }
}
@media (max-width: 767px) {
  .header-example__item:not(:last-child) {
    margin: 0 0 36px 0;
  }
}
.header-example__link {
  font-size: 16px;
  font-weight: 600;
  line-height: 25px;
  transition: color 0.3s ease;
}
@media (max-width: 1023px) {
  .header-example__link {
    color: #ffffff;
    font-size: 20px;
    line-height: 25px;
  }
}
@media (hover: hover) {
  .header-example__link:hover:not(.focus-visible):not(span) {
    color: #2c39f2;
  }
}
.header-example__link--blue {
  color: #2c39f2;
}
@media (hover: hover) {
  .header-example__link--blue:hover:not(.focus-visible):not(span) {
    color: #ff1553;
  }
}
.header-example__burger {
  align-items: center;
  background-color: transparent;
  border: none;
  color: #000000;
  display: none;
  padding: 0;
  position: relative;
  z-index: 25;
}
@media (max-width: 1023px) {
  .header-example__burger {
    display: flex;
    position: relative;
    transition: all 0.3s ease;
  }
}
.header-example__burger .is-open .header-example__burger-text {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease 0s;
}
.header-example__burger .is-open .header-example__burger-icon {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}
.header-example__burger-text {
  cursor: pointer;
  font-family: "Neue Machina", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  transition: opacity 0.3s ease 0.1s;
}
.is-open .header-example__burger-text {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease 0s;
}
.header-example__burger-icon {
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  stroke: #ffffff;
  top: 50%;
  transform: translateY(-50%);
  transition: opacity 0.3s ease;
}
.is-open .header-example__burger-icon {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}

.section-example {
  height: 100vh;
}
.section-example:nth-child(2) {
  background-color: #f03838;
}
.section-example:nth-child(3) {
  background-color: #3c69ff;
}
.section-example:nth-child(5) {
  background-color: #f4ff25;
}
.section-example:nth-child(6) {
  background-color: #17ff45;
}

@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  min-height: 100vh;
  padding: 0;
}

html {
  color: #011c40;
  font-family: "Montserrat", "arial", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  scroll-behavior: smooth;
  text-rendering: optimizeLegibility;
}

body {
  background-color: #ffffff;
  height: 100%;
  width: 100%;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  height: auto;
  max-width: 100%;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  position: relative;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.scroll-lock-ios {
  overflow: hidden;
  position: fixed;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  margin: 0 auto;
  max-width: 1224px;
  padding: 0 32px;
  width: 100%;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.modal {
  background-color: rgba(0, 0, 0, 0.8);
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 0;
  opacity: 0;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  overflow-y: auto;
  pointer-events: none;
  position: fixed;
  right: 0;
  scrollbar-width: none;
  top: 0;
  transform: scale(1.2);
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  visibility: hidden;
  z-index: 1000;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  align-items: center;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  min-height: 100%;
  padding: 60px 40px;
  position: relative;
  width: 100%;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  cursor: pointer;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.modal__content {
  background-color: #ffffff;
  padding: 40px;
  position: relative;
  width: 600px;
}
@media (max-width: 767px) {
  .modal__content {
    padding: 20px;
    width: 100%;
  }
}
.modal__close-btn {
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  height: 40px;
  padding: 0;
  position: absolute;
  right: 22px;
  top: 22px;
  width: 40px;
  z-index: 1;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  opacity: 1;
  pointer-events: auto;
  transform: scale(1);
  visibility: visible;
}

.btn {
  background-color: #2c39f2;
  border: none;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  padding: 10px 20px;
  transition: opacity 0.5s cubic-bezier(0, 2, 1, 1);
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.custom-input,
.custom-textarea {
  display: flex;
  padding: 0 0 20px 0;
  position: relative;
  width: 100%;
}
.custom-input label,
.custom-textarea label {
  align-items: center;
  display: flex;
  width: 100%;
}
.custom-input input, .custom-input textarea,
.custom-textarea input,
.custom-textarea textarea {
  border: 0;
  color: #011c40;
  flex-grow: 1;
  font-size: 14px;
  line-height: 150%;
  margin-left: auto;
  min-height: 43px;
  padding: 11px 16px;
  transition: outline, opacity 0.3s ease;
  width: 100%;
  outline: 2px solid transparent;
  outline-offset: 2px;
  transition: opacity 0.5s cubic-bezier(0, 2, 1, 1);
}
.custom-input input:hover, .custom-input textarea:hover,
.custom-textarea input:hover,
.custom-textarea textarea:hover {
  opacity: 0.7;
}
.custom-input input:active, .custom-input textarea:active,
.custom-textarea input:active,
.custom-textarea textarea:active {
  opacity: 0.5;
}
.custom-input input:focus-visible, .custom-input textarea:focus-visible,
.custom-textarea input:focus-visible,
.custom-textarea textarea:focus-visible {
  outline: 2px solid #f9fbfd;
  outline-offset: 2px;
}
.custom-input input::placeholder, .custom-input textarea::placeholder,
.custom-textarea input::placeholder,
.custom-textarea textarea::placeholder {
  color: rgba(1, 28, 64, 0.7);
  font-size: 14px;
  line-height: 150%;
}
.custom-input__error,
.custom-textarea__error {
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  position: absolute;
  right: 0;
  transition: opacity 0.3s ease;
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  outline-color: #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  outline-color: rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  outline-color: #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  outline-color: rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  bottom: -2px;
  font-size: 12px;
  left: 0;
  position: absolute;
}
.custom-input .input-message--invalid,
.custom-textarea .input-message--invalid {
  color: #ff1553;
}
.custom-input .input-message--valid,
.custom-textarea .input-message--valid {
  color: #15ab07;
}

.custom-toggle {
  align-items: center;
  display: flex;
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  margin-right: 0 0 20px 0;
  min-height: 18px;
  position: relative;
